'use client';

import { useCallback, useEffect, useMemo, useState, type ReactNode } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { AuthForm } from '@/components/AuthForm';
import { AuthUIProvider } from '@/contexts/AuthUIContext';

export function AuthGate({ children }: { children: ReactNode }) {
  const { user, loading, guestMode, enableGuestMode, disableGuestMode } = useAuth();
  const [promptVisible, setPromptVisible] = useState(false);
  const [showLoginForm, setShowLoginForm] = useState(false);

  useEffect(() => {
    if (loading) {
      return;
    }

    if (!user && !guestMode) {
      setPromptVisible(true);
      setShowLoginForm(false);
      return;
    }

    if (user) {
      setPromptVisible(false);
      setShowLoginForm(false);
    }
  }, [guestMode, loading, user]);

  const handleContinueAsGuest = useCallback(() => {
    enableGuestMode();
    setPromptVisible(false);
    setShowLoginForm(false);
  }, [enableGuestMode]);

  const handleShowLogin = useCallback(() => {
    setShowLoginForm(true);
  }, []);

  const handleBackToChoices = useCallback(() => {
    setShowLoginForm(false);
  }, []);

  const requestLogin = useCallback(() => {
    disableGuestMode();
    setPromptVisible(true);
    setShowLoginForm(true);
  }, [disableGuestMode]);

  const providerValue = useMemo(
    () => ({
      requestLogin,
    }),
    [requestLogin]
  );

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gradient-to-br from-blue-50 via-indigo-50 to-purple-100">
        <div className="text-indigo-700 font-medium">Loading your workspace‚Ä¶</div>
      </div>
    );
  }

  return (
    <AuthUIProvider value={providerValue}>
      <div className="relative min-h-screen">
        {children}

        {promptVisible && (
          <div className="fixed inset-0 z-[60] flex items-center justify-center bg-slate-900/40 px-4 py-8 backdrop-blur-sm">
            <div className="w-full max-w-lg rounded-2xl bg-white shadow-2xl border border-indigo-100">
              {showLoginForm ? (
                <div className="p-6">
                  <div className="mb-4 flex items-center justify-between">
                    <div>
                      <h2 className="text-xl font-semibold text-indigo-900">Sign in to save your progress</h2>
                      <p className="text-sm text-indigo-700/80">
                        Your conversations and tools stay synced when you create an account.
                      </p>
                    </div>
                  </div>
                  <AuthForm />
                  <div className="mt-6 flex flex-col gap-3 sm:flex-row sm:justify-between">
                    <button
                      type="button"
                      onClick={handleBackToChoices}
                      className="order-2 inline-flex justify-center rounded-lg border border-indigo-200 px-4 py-2 text-sm font-semibold text-indigo-700 hover:bg-indigo-50 sm:order-1"
                    >
                      Back
                    </button>
                    <button
                      type="button"
                      onClick={handleContinueAsGuest}
                      className="order-1 inline-flex justify-center rounded-lg bg-slate-900 px-4 py-2 text-sm font-semibold text-white hover:bg-slate-800 sm:order-2"
                    >
                      Continue without saving
                    </button>
                  </div>
                </div>
              ) : (
                <div className="p-8 text-center">
                  <h2 className="text-2xl font-semibold text-indigo-900">Want to save your progress?</h2>
                  <p className="mt-3 text-sm text-indigo-700/80">
                    Sign in to keep your mood tracking and conversations available across sessions. Prefer to explore
                    first? You can continue in a temporary guest mode.
                  </p>
                  <div className="mt-8 flex flex-col gap-3 sm:flex-row sm:justify-center">
                    <button
                      type="button"
                      onClick={handleShowLogin}
                      className="inline-flex justify-center rounded-lg bg-indigo-600 px-4 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-700"
                    >
                      Sign in to save
                    </button>
                    <button
                      type="button"
                      onClick={handleContinueAsGuest}
                      className="inline-flex justify-center rounded-lg border border-indigo-200 px-4 py-2 text-sm font-semibold text-indigo-700 hover:bg-indigo-50"
                    >
                      Continue without saving
                    </button>
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </AuthUIProvider>
  );
}
